import React from 'react';
import {NavLink,Route,Redirect,withRouter,BrowserRouter as Router,Switch,Link} from 'react-router-dom';
import './doc.css';
import RouteWithSubRoutes from '../pages/RouteWithSubRoutes';



// const DocContent = (props) => {
//     return <h1>
//         { props.match.params.type }
//     </h1>
// }



const Doc = ({routes}) => {
    return (
        <div className='doc'>

            {/* <NavBar
                mode="light"
                icon={ <Icon type="left" /> }
                onLeftClick={ () => props.history.goBack() }
                rightContent={ [
                    <Icon key="0" type="search" style={ { marginRight: '16px' } } />,
                    <Icon key="1" type="ellipsis" />,
                ] }
            >NavBar</NavBar> */}


            <div className="content">
                {/* 嵌套路由 */ }
                {/* <Route path='/doc/:type' component={ DocContent } /> */}

                <Redirect from='/doc' to='/doc/core' />

                <Switch>
                    {routes.map((route, i) => (
                    <RouteWithSubRoutes key={i} {...route} />
                    ))}
                </Switch>
                
            </div>

            <ol>
                <li><Link to='/doc/core'>核心概念</Link></li>
                <li><Link to='/doc/guide'>高级指引</Link></li>
                <li><Link to='/doc/api'>API</Link></li>
                <li><Link to='/doc/hooks'>Hooks</Link></li>
            </ol>

            
            

        </div>
    )
}



export default withRouter(Doc);