import React from 'react';
import Home1 from './pages/Home1';
import Doc from './pages/Doc';
import Tuto from './pages/Tuto';
import Blog from './pages/Blog';
import Comm from './pages/Comm';
import Login from './pages/Login';

const routes = [
    {
        path: '/',
        exact: true,
        component: Home1
    },
    {
        path: '/doc',
        component: Doc,
        pri: true,
        routes: [

            // {
            //     path: '/doc/:type',
            //     component : Doc
            // }

            {
                path: '/doc/core',
                component: Core,
            },
            {
                path: '/doc/guide',
                component: Guide,
            },
            {
                path: '/doc/api',
                component: Api,
            },
            {
                path: '/doc/hooks',
                component: Hooks,
            },

        ]
    },
    {
        path: '/tuto',
        component: Tuto,
        pri: true
    },
    {
        path: '/blog',
        component: Blog
    },
    {
        path: '/comm',
        component: Comm
    },
    {
        path: '/login',
        component: Login
    },
]


function Core(){
    return(
        <h1>core</h1>
    )
}
function Guide(){
    return(
        <h1>guide</h1>
    )
}
function Api(){
    return(
        <h1>api</h1>
    )
}
function Hooks(){
    return(
        <h1>hooks</h1>
    )
}

export default routes;